import { Fragment, useState } from "react"
import { LlmEngineSource } from "@rp/react/tanstack-api/llm-summary"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, Collapse, Divider, Typography, alpha } from "@rp/react/web/mui/core"
import { Info, InfoOutlined } from "@rp/react/web/mui/icons"

const InfoIcon = ({ onClick, expanded }: { onClick: () => void; expanded: boolean }) => {
  const [iconHovered, setIconHovered] = useState(false)

  return expanded || iconHovered ? (
    <Info
      onMouseLeave={() => setIconHovered(false)}
      onClick={onClick}
      sx={{ color: "text.primary" }}
    />
  ) : (
    <InfoOutlined
      onMouseEnter={() => setIconHovered(true)}
      onClick={onClick}
      sx={{ color: "grey.300" }}
    />
  )
}

type Props = {
  explainer: string
  sources: Array<LlmEngineSource>
}

export const EngineChartSources = ({ explainer, sources }: Props) => {
  const [expanded, setExpanded] = useState(false)
  const { isMobile } = useDeviceInfo()

  return (
    <Box>
      <Box sx={{ width: "auto", display: "flex" }}>
        <Box
          onClick={isMobile ? () => setExpanded(prev => !prev) : undefined}
          sx={{
            borderRadius: "2px",
            border: isMobile ? `1px solid ${alpha("#ffffff", 0.12)}` : undefined,
            width: "auto",
            display: "flex",
            p: "6px",
            gap: 1,
            alignItems: "center",
            backgroundColor: expanded && isMobile ? `${alpha("#ffffff", 0.08)}` : undefined,
            "& svg": { cursor: "pointer", height: 20, width: 20 },
          }}
        >
          {sources.map((source, index, array) => {
            return (
              <Fragment key={source.name}>
                <>
                  <img src={source.faviconUrl} alt={source.name} width={16} height={16} />
                  <Typography
                    sx={{ fontSize: 12, color: `${alpha("#ffffff", 0.87)}`, lineHeight: "16px" }}
                  >
                    {source.name}
                  </Typography>
                </>

                {index !== array.length - 1 && (
                  <Divider
                    flexItem
                    orientation="vertical"
                    sx={{ borderColor: `${alpha("#ffffff", 0.32)}` }}
                  />
                )}
              </Fragment>
            )
          })}
          {isMobile ? null : (
            <InfoIcon expanded={expanded} onClick={() => setExpanded(prev => !prev)} />
          )}
        </Box>
      </Box>
      <Collapse in={expanded}>
        <Typography sx={{ mt: 1, fontSize: 18, lineHeight: "24px" }}>{explainer}</Typography>
      </Collapse>
    </Box>
  )
}
